@font-face {
    font-family: 'PFStardustExtraBold';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2506-1@1.0/PFStardustExtraBold.woff2') format('woff2');
    font-weight: 800;
    font-style: normal;
}
@font-face {
    font-family: 'RixInooAriDuriR';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2207-01@1.0/RixInooAriDuriR.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

* {margin: 0; padding: 0; box-sizing: border-box; cursor: none; }
.cursor { position: fixed; background: #72bbff;width: 20px;height: 20px; margin: 0; border-radius: 50%; will-change: transform; user-select: none; pointer-events: none; z-index: 10000; transition: background-color linear 0.2s;}

#header {width: 100vw; height: 60px; display: flex; z-index: 999; align-items: center; padding: 20px 16px; position: fixed; top: 0; left: 0; }
#header .logo {font-family: 'PFStardustExtraBold',sans-serif; font-size: 22px; line-height: normal; letter-spacing: -0.195px; transition: all linear 0.2s;  }
#header nav {font-family: 'Pretendard-Regular',sans-serif; margin-left: auto; display: flex; align-items: center; gap: 20px; transition: all linear 0.2s;}
#header nav:has(a:hover) {gap: 30px;}
#header nav a {font-size: 15px; text-decoration: none; transition: all linear 0.2s;}
#header nav a:hover{transform: scale(1.5); font-weight: bold;}

.fp-watermark {display: none !important; opacity: 0 !important; visibility: hidden !important;}
.fp-overflow {width: 100%; height: 100%;}
.section_wrap {width: 100%; height: 100%; padding: 10vh 8vw;}

#cover {cursor: none; font-family: 'RixInooAriDuriR',sans-serif;}
#cover .shapes { position: relative; height: 100%; width: 100%; background: #72bbff; overflow: hidden;}
#cover .shape {position: absolute; border-radius: 50%;margin : 0;   will-change: left, top;}
#cover .shape-1 {background: #72bbff; width: 40vw; aspect-ratio: 1/1;}
#cover .shape-2 { background: #588e34; width: 35vw; aspect-ratio: 1/1;}
#cover .shape-3 { background: #ffc4bc; width: 15vw; aspect-ratio: 1/1;}
#cover .text_wrap { position: absolute; top: 0; left: 0; height: 100%; width: 100%; display: flex; flex-direction: column; justify-content: center; align-items: center; background: #fff; mix-blend-mode: screen; }
#cover h1 { font-size: 10vw; color: #000; margin: 0; text-align: center; }
#cover p { font-size: 4vw;}
#cover ::selection { color: #fff; background: #72bbff; }


#about {background-color: #fff; font-family: 'Pretendard-Regular',sans-serif; overflow: hidden; }
#about #profile_wrap {position: relative; z-index: 1; display: grid;
    width: 100%;
    height: 100%;
    gap:10px 40px;
    grid-template-columns: 1.5fr 3fr 1fr; 
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-areas:
      "profile_box skill_box skill_box"
      "career_box all_info all_info"
      "career_box all_info all_info"
      "career_box all_info all_info";
    }
.circle {position: absolute;}
.dark {bottom: -10vw; left: -10vw; width: 50vw; aspect-ratio: 1/1; border-radius: 50vw; background-color: #266fb3; filter: blur(10vw);}
.normal {top: 50%; right: 5vw; width: 30vw; aspect-ratio: 1/1; border-radius: 30vw; background-color: #72bbff; filter: blur(10vw); }
.light {top: -20%; left: 40vw; width: 50vw; aspect-ratio: 1/1; border-radius: 50vw; background-color: #89ecaf; filter: blur(20vw);}


#about .profile_box {width: auto; position: relative;  grid-area: profile_box;}
#about .profile_box .bubble { position: absolute; top: 0; left: 50%; transform: translate(-50%,0); background-color: #fff; border-radius: 20px; width: auto; min-width: 280px; color: #266fb3; font-size: 20px; font-weight: 600; line-height: normal; letter-spacing: -0.195px; padding: 10px 15px 8px;}
#about .profile_box .bubble::after {content: ""; display: block; width: 20px; height: 20px; background-color: #fff; border-radius: 20px; position: absolute; top: 35px; left: 40px;}
#about .profile_box .bubble::before {content: ""; display: block; width: 10px; height: 10px; background-color: #fff; border-radius: 10px; position: absolute; top:55px; left: 55px;}
#about .profile_box .profile_img {width: 100%; height: auto; min-height: 200px; padding-top: 30px; overflow: hidden; display: flex; justify-content: center; flex-direction: column; align-items: center; }
#about .profile_box .profile_img .img_wrap {width: 60%;}
#about .profile_box .profile_img .img_wrap >img {width: 100%; height: auto;}
#about .profile_box .profile_img .name {font-size: 20px; font-weight: 600; padding: 0 15px; text-align: center; color: #fff;}
#about .profile_box .profile_img .name span {font-size: 14px; font-weight: 400; }

#about .career_box { grid-area: career_box; padding: 24px; color: #fff;} 
#about .career_box ul {display: flex; flex-direction: column; gap: 10px;}
#about .career_box ul li{display: flex; flex-wrap: wrap; align-items: flex-start; gap: 8px; font-size: 18px; line-height: 26px; letter-spacing: -0.225px; word-break: keep-all; }
#about .career_box ul li p{width: auto; flex: 1 1 50%;}

#about .skill_box { width: 100%; grid-area: skill_box;}
#about .skill_box .storage-widget { background: #fff; border-radius: 12px; padding: 24px; width: 100%; margin: auto; box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);}
#about .skill_box .storage-header { display: flex; justify-content: space-between; align-items: center;  margin-bottom: 16px;}
#about .skill_box .storage-title { font-weight: bold; color: #266fb3;}
#about .skill_box .upgrade-button { display: flex; align-items: center; gap: 4px; font-size: 13px; border: 1px solid #00bcd4; background: #e0f7fa; color: #007c91; border-radius: 999px; padding: 4px 10px; cursor: pointer; }
#about .skill_box .upgrade-button:hover { background: #b2ebf2; }
#about .skill_box .icon { fill: currentColor; }
#about .skill_box .storage-total { font-size: 28px; font-weight: 500; margin-bottom: 20px; }
#about .skill_box .storage-total sup { font-size: 14px; color: #888; margin-left: 10px; position: relative; top: -0.5em; }
#about .skill_box .storage-bar-graph { height: 20px; position: relative; background: #e5e7eb; border-radius: 999px; overflow: hidden; margin-bottom: 20px; }
#about .skill_box .bar-wrapper { position: absolute; top: 0; left: 0; right: 0; height: 100%; }
#about .skill_box .bar-container { position: absolute; top: 0; height: 100%; width: 100%; transition: transform 0.7s; }
#about .skill_box .bar { position: absolute; top: 0; height: 100%; transition: all 0.3s ease; }
#about .skill_box .html { background-color: #e34f26; }
#about .skill_box .css { background-color: #1572B6; }
#about .skill_box .js { background-color: #f7df1e; }
#about .skill_box .jquery { background-color: #073f68; }
#about .skill_box .react { background-color: #61dafb; }
#about .skill_box .bg-gray { background-color: #6b7280; }
#about .skill_box .bg-default { background-color: #e5e7eb; }
#about .skill_box .storage-category-list { display: flex; flex-wrap: wrap; gap: 10px 20px; font-size: 14px; }
#about .skill_box .storage-category { display: flex; align-items: center; gap: 6px; }
#about .skill_box .color-box { width: 16px; height: 12px; border-radius: 2px; }


#about .all_info {grid-area: all_info; background-color: #fff; border-radius: 20px;  padding: 40px 40px;}
#about .all_info .cart_wrap {width: 300px;}



  
  